import Chip from '@mui/material/Chip';
import Stack from '@mui/material/Stack';

export const RoleChip = (label, color, hasRole) => {
  return (
    <Chip 
      label={label}
      size="small"
      color={hasRole ? color : 'default'}
      variant={hasRole ? 'filled' : 'outlined'} 
    />
  )
}

export const RoleChipStack = (roles) => {
  return (
    <Stack spacing={1} alignItems="center">
      <RoleChip label="" color="info" hasRole={roles.hasTank} />
      <RoleChip label="" color="success" hasRole={roles.hasHealer} />
      <RoleChip label="" color="warning" hasRole={roles.hasDD} />
      <RoleChip label="PvP" color="secondary" hasRole={roles.hasPvP} />
    </Stack>
  );
}